import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import "./../styles/place-graphic.scss"

export default class ThemeGraphic extends React.Component {
    state = {}
    render() {
        return (

<StaticQuery
    query={graphql`
        query {
            allFile(filter: {relativeDirectory: {eq: "img"}}) {
                edges {
                    node {
                        relativePath
                        childImageSharp {
                            fixed(width: 500) {
                                ...GatsbyImageSharpFixed
                            }
                        }
                    }
                }
            }
        }
    `}
    render={data => {
        const theme_data = [
{
	theme: 'animals',
	filename: 'DSC_0579.jpg',
	link: '/animals'
},
{
	theme: 'food',
	filename: 'IMG_4497.jpg',
	link: '/food'
},
{
	theme: 'home',
	filename: 'thumb__MG_4729_1024.jpg',
	link: '/home'
},
{
	theme: 'portraits',
	filename: 'IMG_6094.jpg',
	link: '/portraits'
},
{
	theme: 'plants',
	filename: 'thumb__MG_4335_1024.jpg',
	link: '/plants'
},
{
	theme: 'sky',
	filename: '_MG_5660.jpg',
	link: '/sky'
},
{
	theme: 'structure',
	filename: '_MG_7122.jpg',
	link: '/structure'
},
{
	theme: 'topography',
	filename: '_MG_5784.jpg',
	link: '/topography'
}
        ]
        return (
            <div id="place-graphic-wrapper">
            {
                theme_data.map((place, index) =>
                    <div className="place-link" key={index}>
                        <Link to={"/themes" + place.link}>
                            <Img
                                fixed={data.allFile.edges.find(({node}) => node.relativePath === 'img/' + place.filename).node.childImageSharp.fixed}
                                style={{width: '100%', height: '100%'}}
                            />
                            <div className="caption">
                                <div className="caption-inner">
                                    <span class="border inner-right"></span>
                                    <span class="border inner-left"></span>
                                    <span class="border inner-bottom"></span>
                                    <span class="border inner-top"></span>
                                    {place.theme}
                                </div>
                            </div>
                        </Link>
                    </div>
                )
            }
            </div>
        )
    }}
/>

           )
    }
}

